var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

test.serial('success open', function (t) {
  return request.post('/')
    .send({
      "host": "113.172.82.132",
      "user": "support",
      "pass": "support",
      "tunport": 1080
    })
    .expect(200)
    .expect(function (res) {
      t.is(res.body.status, 'Connected');
    });
});

test.serial('success close', function (t) {
  return request.post('/')
    .send({
      host: "113.172.82.132",
      tunport: 1080,
      close: 1
    })
    .expect(200)
    .expect(function (res) {
      console.log(res.body);
      t.is(res.body.status, 'Connection closed');
    });
});
